#contact > div {
  height: 70%;
  display: flex;
  align-items: center;
}

#contact > div > form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90vw;
  max-width: 450px;
}
form > input {
  width: 100%;
  height: 35px;
  margin: 5px 0px;
  outline: none;
  border: none;
  border-radius: 8px;
  padding: 7px;
}
form > textarea {
  height: 200px;
  width: 100%;
  resize: none;
  margin: 5px 0px;
  outline: none;
  border: none;
  border-radius: 8px;
  padding: 7px;
  font-size: 13.7px;
}
form > button {
  width: 70%;
  height: 42px;
  margin: 10px;
  background-color: #b485ec;
  border-radius: 8px;
  font-size: 15px;
  border: #b485ec;
}
.links {
  width: 60vw;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 60px;
}
.links > div {
  width: 170%;
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: left;
}
.links > div > div {
  height: 40px;
  width: 40px;
  background-color: #d2d9e2;
  border-radius: 10px;
  margin: 0px 15px;
}
.links > div > a {
  color: #8f54d9;
  font-size: 16.6px;
}
#github {
  background-image: url("./github.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
#linkedin {
  background-image: url("./linkedin.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
#codepen {
  background-image: url("./codepen.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media screen and (max-width: 750px) {
  #contact {
    flex-direction: column;
  }
}
